@mixin border($s: 1px, $c: rgb(0, 0, 0)) {
  border: $s solid $c;
}

@mixin flex($direction: row, $align-items: center, $justify-content: 'center') {
  display: flex;
  flex-direction: $direction;
  align-items: $align-items;
  justify-content: $justify-content;
}

@mixin box-shadow($size: 2px, $color: rgba(0, 0, 0, 0.541)) {
  box-shadow: 0 0 $size $color;
}

@mixin cursor($t: pointer) {
  cursor: $t;
}

@mixin b-radius($s: 5px) {
  border-radius: $s;
}

@mixin center() {
  display: flex;
  align-items: center;
  justify-content: center;
}
