@import '@base';

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

button {
  background-color: inherit;
  border: none;
  outline: none;
}

input {
  border: none;
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style-type: none;
}
