@import '@base';

$search-btn-color: rgba(103, 183, 113, 1);
$search-btn-hover-color: rgb(70, 180, 84);

$categories-btn-color: $search-btn-color;
$categories-btn-hover-color: $search-btn-hover-color;

$btn-top-right-circle-color: $search-btn-color;

$header-bottom-height: 40px;

$btn-hover-color: rgba(250, 251, 252, 1);

.Header {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.322);
}

.logo {
  font-size: 30px;
}

.top {
  padding: 20px 0;
  padding-left: 100px;
  padding-right: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(128, 128, 128, 0.61);
}

.top-right {
  display: flex;
  justify-content: flex-start;
  height: 50px;
}

.products-btn {
  @include center;
  @include box-shadow;
  padding: 0 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  background-color: rgba(250, 251, 252, 1);

  &-icon {
    margin-left: 20px;
    margin-top: -5px;
  }
}

.search-panel {
  @include box-shadow;
  min-width: 500px;
  display: flex;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.search-input {
  flex-grow: 1;
  display: flex;
  align-items: center;
  text-indent: 15px;
  background-color: rgba(250, 251, 252, 1);
}

.search-btn {
  padding: 0 15px;
  display: flex;
  align-items: center;
  background-color: $search-btn-color;
  color: white;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  cursor: pointer;
  
  &:hover {
    background-color: $search-btn-hover-color;
  }
}

.usd-btn {
  @include box-shadow;
  padding: 0 10px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 5px;
  font-weight: 600;

  &-icon {
    margin-left: 20px;
    margin-top: -10px;
  }
}

.lang-btn {
  @include box-shadow;
  margin-left: 10px;
  padding: 0 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  font-weight: 600;

  &-text {
    display: flex;
    align-items: center;
  }
  &-icon {
    margin-left: 20px;
    margin-top: -7px;
    height: 100%;
  }
}

//-----------------------------------------------

.bottom {
  display: flex;
  padding: 10px 100px 10px 100px;
  align-items: center;
  justify-content: space-between;
}

.bottom-left {
  display: flex;
  align-items: center;
  height: $header-bottom-height;
}

.categories-btn {
  padding: 0 20px 0 20px;
  height: 100%;
  background-color: $categories-btn-color;
  color: white;
  border-radius: 5px;
  font-weight: 600;
  cursor: pointer;

  &:hover {
    background-color: $categories-btn-hover-color;
  }
}

.login-btn {
  @include box-shadow;
  margin-left: 30px;
  padding: 0 15px;
  height: 100%;
  border-radius: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;

  &:hover {
    background-color: $btn-hover-color;
  }
}

.login-btn-icon {
  margin-right: 10px;
  font-size: 20px;
}

%btn {
  @include box-shadow;
  @include cursor;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  height: 100%;
  width: $header-bottom-height;
  position: relative;

  &:hover {
    background-color: $btn-hover-color;
  }
}

.bottom-right {
  display: flex;
  align-items: center;
  height: $header-bottom-height;

  & > * {
    margin-left: 20px;

    &:nth-child(1) {
      margin-left: 0;
    }
  }
}

.messages-btn {
  @extend %btn;
}

.like-btn {
  @extend %btn;
}

.korzina-btn {
  @extend %btn;
  padding: 0 15px 0 10px;
  font-size: 13px;
  width: initial;
}

.korzina-btn-info {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
}

.btn-top-right-circle {
  width: 20px;
  height: 20px;
  background-color: $btn-top-right-circle-color;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(35%, -35%);
  color: white;
  font-size: 13px;
}

.bottom-right-btn-icon {
  font-size: 20px;
}
