@import '@base';

$nav-link-selected-color: rgba(103, 183, 113, 1);
$bottom-line-color: $nav-link-selected-color;

.NavLink {
  margin-left: 20px;
  font-size: 13px;
  position: relative;

  &:nth-child(1) {
    margin-left: 0;
  }
  &.selected {
    color: $nav-link-selected-color;

    & .bottom-line {
      display: block;
    }
  }
}

.bottom-line {
  @include b-radius(15px);
  display: none;
  background-color: $bottom-line-color;
  position: absolute;
  bottom: -10px;
  height: 2px;
  width: 100%;
}
